body,
html {
  background-color: #fff;
  position: relative;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
.clear:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.clear {
  zoom: 1;
}
.pos-r {
  position: relative;
}
.hide {
  display: none;
}
a,
a:hover {
  text-decoration: none;
}
body {
  background-color: #0128AB;
}
html {
  background-color: #0128AB;
}
.bjtop {
  position: relative;
  width: 100%;
  height: 8.84rem;
  background: url('../../img/loginbj.png') no-repeat;
  background-size: cover;
}
.bjtop1 {
  position: relative;
  width: 100%;
  height: 4.73333333rem;
  background: url('../../img/loginbj1.png') no-repeat;
  background-size: cover;
}
.bjtop2 {
  position: relative;
  width: 100%;
  height: 8.84rem;
  background: url('../../img/loginbj2.png') no-repeat;
  background-size: cover;
}
.bjtop3 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 7.06666667rem;
  background: url('../../img/loginbj3.png') no-repeat;
  background-size: cover;
}
.bjtop3 .but {
  position: absolute;
  bottom: 1.06666667rem;
  left: 25%;
  width: 50%;
  border: 2px solid #fff;
  background-color: #F86A16;
  padding: 0.21333333rem 0;
  border-radius: 0.8rem;
  text-align: center;
  font-size: 20px;
  color: #fff;
}
[data-dpr="2"] .bjtop3 .but {
  font-size: 40px;
}
[data-dpr="3"] .bjtop3 .but {
  font-size: 60px;
}
.content {
  position: absolute;
  top: 26%;
  left: 15%;
  width: 70%;
  z-index: 99;
}
.content li {
  position: relative;
  display: flex;
}
.content li div {
  padding: 0.33333333rem 0;
  width: 100%;
  display: flex;
  border-radius: 0.13333333rem;
  position: relative;
}
.content li div:after {
  position: absolute;
  content: '';
  left: 1.6rem;
  bottom: 0.26666667rem;
  width: 4.26666667rem;
  height: 1px;
  background: #FFE389;
}
.content label {
  position: relative;
  width: 1.6rem;
  white-space: nowrap;
  margin-right: 0.26666667rem;
  font-size: 16px;
  color: #FFE389;
  line-height: 0.53333333rem;
  height: 0.53333333rem;
}
[data-dpr="2"] .content label {
  font-size: 32px;
}
[data-dpr="3"] .content label {
  font-size: 48px;
}
.content input {
  flex: 1;
  border: none;
  background-color: inherit;
  line-height: 0.53333333rem;
  height: 0.53333333rem;
  padding-left: 0.26666667rem;
  color: #fff;
  font-size: 16px;
}
[data-dpr="2"] .content input {
  font-size: 32px;
}
[data-dpr="3"] .content input {
  font-size: 48px;
}
.content .but {
  display: block;
  margin-top: 0.66666667rem;
  background-color: #c01e21;
  text-align: center;
  color: #fff;
  padding: 0.4rem;
  font-size: 18px;
  border-radius: 1.33333333rem;
}
[data-dpr="2"] .content .but {
  font-size: 36px;
}
[data-dpr="3"] .content .but {
  font-size: 54px;
}
.content .image {
  width: 2.66666667rem;
  height: 2.66666667rem;
  margin: 0 auto;
}
.content .image:after {
  width: 0;
}
.content .image span {
  display: inline-block;
  position: relative;
  width: 1.6rem;
  text-align: right;
  margin-right: 0.26666667rem;
  font-size: 16px;
  color: #c01e21;
  line-height: 0.53333333rem;
  height: 0.53333333rem;
}
[data-dpr="2"] .content .image span {
  font-size: 32px;
}
[data-dpr="3"] .content .image span {
  font-size: 48px;
}
.content .image span:last-child {
  width: 1.6rem;
  height: 1.6rem;
}
.content .image img {
  width: 2.66666667rem;
  height: 2.66666667rem;
}
.itemcontent {
  width: 80%;
  margin: 0 auto;
}
.itemcontent h1 {
  text-align: center;
  font-size: 18px;
  padding-bottom: 0.13333333rem;
}
[data-dpr="2"] .itemcontent h1 {
  font-size: 36px;
}
[data-dpr="3"] .itemcontent h1 {
  font-size: 54px;
}
.itemcontent dl {
  width: 100%;
  padding-bottom: 0.13333333rem;
}
.itemcontent dt {
  font-size: 16px;
  color: #B22223;
  padding-bottom: 0.13333333rem;
}
[data-dpr="2"] .itemcontent dt {
  font-size: 32px;
}
[data-dpr="3"] .itemcontent dt {
  font-size: 48px;
}
.itemcontent dd {
  font-size: 14px;
  text-indent: 1em;
  padding-bottom: 0.13333333rem;
}
[data-dpr="2"] .itemcontent dd {
  font-size: 28px;
}
[data-dpr="3"] .itemcontent dd {
  font-size: 42px;
}
.itemcontent dd.indent {
  text-indent: 2em;
}
.logincontent {
  position: absolute;
  top: 40%;
  left: 15%;
  width: 70%;
  z-index: 99;
  text-align: center;
}
.logincontent img {
  width: 2.66666667rem;
  height: 2.66666667rem;
  border-radius: 100%;
  margin-bottom: 0.4rem;
  box-shadow: 1px 1px 40px rgba(0, 0, 0, 0.6);
}
.logincontent p {
  color: #FFE389;
  font-size: 20px;
  letter-spacing: 2px;
}
[data-dpr="2"] .logincontent p {
  font-size: 40px;
}
[data-dpr="3"] .logincontent p {
  font-size: 60px;
}
.gobut {
  position: absolute;
  bottom: 0.8rem;
  left: 30%;
  width: 40%;
  border: 4px solid #fff;
  background-color: #F86A16;
  padding: 0.26666667rem 1.6rem;
  border-radius: 0.8rem;
}
